export default function Accodion() {
  return (
    <MDBAccordion alwaysOpen initialActive={1}>
      {QUESTIONS.map((question) => (
        <MDBAccordionItem
          collapseId={question.id}
          headerTitle={question.question}
          key={question.id}
        >
          <strong>{question.answer}</strong>
        </MDBAccordionItem>
      ))}
    </MDBAccordion>
  );
}
